import { basicSetup, getUserInfo } from "@/api";
export default {
  data() {
    return {
      uid: "",
      avatarPath: "",
      name: ""
    };
  },
  onLoad(options) {
    this.uid = options.uid;
    console.log(this.uid);
  },
  methods: {
    submit() {
      // 提交用户头像与用户昵称
      if (!this.avatarPath) {
        wx.showToast({
          title: "请选择您的头像", //提示的内容,
          icon: "none", //图标,
          duration: 1500, //延迟时间,
          mask: false //显示透明蒙层，防止触摸穿透,
        });
        return;
      }
      if (!this.name) {
        wx.showToast({
          title: "请设置您的昵称", //提示的内容,
          icon: "none", //图标,
          duration: 1500, //延迟时间,
          mask: false //显示透明蒙层，防止触摸穿透,
        });
        return;
      }
      let data = {
        uid: this.uid,
        avatar: this.avatarPath,
        uname: this.name
      };
      // 基本设置
      basicSetup(data).then(res => {
        uni.setStorageSync("UID", res.msg);
        let data = {
          uid: uni.getStorageSync("UID")
        };
        // 获取用户信息
        getUserInfo(data).then(res => {
          console.log(res);
          if (res.ok) {
            uni.setStorageSync("USERINFO", res.data);
          }
        });
        if (res.ok) {
          uni.showToast({
            title: "设置成功", //提示的内容,
            icon: "none", //图标,
            duration: 1500, //延迟时间,
            mask: true //显示透明蒙层，防止触摸穿透,
          });
          setTimeout(() => {
            wx.redirectTo({ url: "/pages/plaza" });
          }, 1200);
        }
      });
    },
    chooseImage() {
      // 上传头像
      uni.chooseImage({
        success: chooseImageRes => {
          const tempFilePaths = chooseImageRes.tempFilePaths;
          uni.uploadFile({
            url: this.$HTTP +"/user/uploadFile", //仅为示例，非真实的接口地址
            filePath: tempFilePaths[0],
            name: "file",
            success: uploadFileRes => {
              console.log(uploadFileRes);
              if (uploadFileRes.statusCode == 200) {
                this.avatarPath = JSON.parse(uploadFileRes.data).filePath;
                console.log(this.avatarPath);
              }
            }
          });
        }
      });
    }
  }
};
